import React from "react";
import ReactDOM from "react-dom";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import darkBaseTheme from "material-ui/styles/baseThemes/darkBaseTheme";
import Divider from "material-ui/Divider";
import Paper from "material-ui/Paper";
import TextField from "material-ui/TextField";
import AppBar from "material-ui/AppBar";

import getMuiTheme from "material-ui/styles/getMuiTheme";
import "./index.css";

const TopBar = () => (
  <AppBar className="topBar" title={<span>Tabi-Tools v.01</span>} />
);
class MapRoute extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      firstLine: "",
      firstStation: "",
      endStation: "",
      cost: "",
      time: "",
      url: ""
    };

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }

  render() {
    return (
      <div className="grid-container">
        <div className="grid-item item1">
          <Paper zDepth={2}>
            <TextField
              name="firstStation"
              hintText="First Station"
              underlineShow={false}
              value={this.state.firstStation}
              onChange={this.handleChange}
              className="textInput"
            />
            <Divider />

            <TextField
              name="endStation"
              hintText="End Station"
              underlineShow={false}
              value={this.state.endStation}
              onChange={this.handleChange}
              className="textInput"
            />
            <Divider />
            <TextField
              name="firstLine"
              hintText="First Train Line"
              underlineShow={false}
              value={this.state.firstLine}
              onChange={this.handleChange}
              className="textInput"
            />
            <Divider />
            <TextField
              name="time"
              hintText="Time"
              underlineShow={false}
              value={this.state.time}
              onChange={this.handleChange}
              className="textInput"
            />
            <Divider />
            <TextField
              name="cost"
              hintText="Cost"
              underlineShow={false}
              value={this.state.cost}
              onChange={this.handleChange}
              className="textInput"
            />
            <Divider />

            <TextField
              name="url"
              hintText="Place shortened URL here"
              underlineShow={false}
              value={this.state.url}
              onChange={this.handleChange}
              className="textInput"
            />
            <Divider />
          </Paper>
        </div>
        <div class="grid-item item2">
          <Paper zDepth={2}>
            Helpful Links:
            <a
              href="https://tabelog.com/en/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <h1>Tabelog</h1>
            </a>
            <a
              href="https://www.jnto.go.jp/eng/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <h1>National Tourism Organization</h1>
            </a>
            <a
              href="http://www.hyperdia.com/en/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <h1>Hyperdia</h1>
            </a>
            <a
              href="https://www.google.com/maps/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <h1>Google Maps</h1>
            </a>
            <a
              href="https://fastjapan.com/en/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <h1>Fast Japan</h1>
            </a>
          </Paper>
        </div>
        <div class="grid-item item3">
          {" "}
          <Paper zDepth={2}>
            <h1>
              ◆{this.state.firstStation} → {this.state.endStation}
            </h1>
            <p />
            <h1>Train: {this.state.firstLine}</h1>
            <h1>Time: {this.state.time} Mins</h1>
            <h1>Fare: {this.state.cost} Yen</h1>
            <p />
            <h1>Thank you for using Tabiko.</h1>
            <p />
            <h1>URL: {this.state.url}</h1>
          </Paper>
        </div>
      </div>
    );
  }
}

class MainUI extends React.Component {
  render() {
    return (
      <div className="container ">
        <MuiThemeProvider>
          <TopBar />
        </MuiThemeProvider>
        <div>
          <MuiThemeProvider muiTheme={getMuiTheme(darkBaseTheme)}>
            <MapRoute />
          </MuiThemeProvider>
        </div>
      </div>
    );
  }
}

ReactDOM.render(
  <div>
    <MainUI />
  </div>,
  document.getElementById("root")
);



